.wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: white;
}

.Logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: 256%;
}

.Log {
  float: left;
}

.login-form {
  width: 100%;
  max-width: 400px;
  padding: 15px;
  margin: auto;
  height: 100%;
}

.form-wrapper {
  width: 500px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: #ffffff;
  float: right;
  padding-left: 150px;
  padding-right: 0px;
}

.ok {
  padding-left: 100px;
}

h1 {
  text-align: center;
  width: 100%;
  color: #111;
  font-weight: lighter;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

label {
  font-size: 15px;
  margin-bottom: 0.25em;
  color: #222;
  font-weight: lighter;
}

input {
  padding: 12px 20px;
  margin: 8px 0;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

input::-webkit-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input:-ms-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input::-ms-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input::placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input.error {
  border: 1px solid red;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
}

.firstName {
  margin-right: 1%;
}

.lastName {
  margin-left: 1%;
}

.firstName,
.lastName,
.email,
.password {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.email,
.password {
  width: 100%;
}

.createAccount {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.createAccount button {
  background-color: #48b4e0;
  color: #fff;
  border: 2px solid #fff;
  width: 100%;
  margin-top: 1em;
  padding: 8px 0px;
  font-size: 1em;
  font-weight: lighter;
  letter-spacing: 1px;
  margin-bottom: 0.25em;
}

.createAccount button:hover {
  color: #48b4e0;
  background-color: #fff;
  border: 2px solid #48b4e0;
}

.createAccount small {
  color: #999;
  font-weight: lighter;
}
.head {
  padding-top: 100px;
  padding-bottom: 25px;
}

